<template>
  <div id="app">
    <div class="el-box">
      <el-form :model="ruleFormA" :rules="rules" ref="ruleFormA" label-width="100px" class="demo-ruleForm">
        <el-form-item label="version" prop="version">
          <el-input v-model="ruleFormA.version" placeholder="SimpleWallet1.0"></el-input>
        </el-form-item>
        <el-form-item label="appName" prop="appName">
          <el-input v-model="ruleFormA.appName" placeholder="Dapp"></el-input>
        </el-form-item>
        <el-form-item label="appIcon" prop="appIcon">
          <el-input v-model="ruleFormA.appIcon" placeholder="Dapp"></el-input>
        </el-form-item>
        <el-form-item label="action" prop="action">
          <el-input v-model="ruleFormA.action" placeholder="login"></el-input>
        </el-form-item>
        <el-form-item label="uuID" prop="uuID">
          <el-input v-model="ruleFormA.uuID" placeholder="Dapp server"></el-input>
        </el-form-item>
        <el-form-item label="loginUrl" prop="loginUrl">
          <el-input v-model="ruleFormA.loginUrl" placeholder="Dapp serverurl"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleFormA')"></el-button>
          <el-button @click="resetForm('ruleFormA')"></el-button>
        </el-form-item>
      </el-form>
      <QRCode v-show="showCode" :text="qrCode"></QRCode>

      <el-form :model="ruleFormB" :rules="rules" ref="ruleFormB" label-width="100px" class="demo-ruleForm">
        <el-form-item label="version" prop="version">
          <el-input v-model="ruleFormB.version" placeholder="SimpleWallet1.0"></el-input>
        </el-form-item>
        <el-form-item label="timestamp" prop="timestamp">
          <el-input v-model="ruleFormB.timestamp" placeholder="UNIX"></el-input>
        </el-form-item>
        <el-form-item label="sign" prop="sign">
          <el-input v-model="ruleFormB.sign" placeholder="eos"></el-input>
        </el-form-item>
        <el-form-item label="uuID" prop="uuID">
          <el-input v-model="ruleFormB.uuID" placeholder="Dapp server"></el-input>
        </el-form-item>
        <el-form-item label="account" prop="account">
          <el-input v-model="ruleFormB.account" placeholder="eos"></el-input>
        </el-form-item>
        <el-form-item label="ref" prop="ref">
          <el-input v-model="ruleFormB.ref" placeholder=","></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm_v2('ruleFormB')"></el-button>
          <el-button @click="resetForm('ruleFormB')"></el-button>
        </el-form-item>
      </el-form>

      <el-form :model="ruleFormC" :rules="rules" ref="ruleFormC" label-width="100px" class="demo-ruleForm">
        <el-form-item label="version" prop="version">
          <el-input v-model="ruleFormC.version" placeholder="SimpleWallet1.0"></el-input>
        </el-form-item>
        <el-form-item label="appName" prop="appName">
          <el-input v-model="ruleFormC.appName" placeholder="Dapp"></el-input>
        </el-form-item>
        <el-form-item label="appIcon" prop="appIcon">
          <el-input v-model="ruleFormC.appIcon" placeholder="Dapp"></el-input>
        </el-form-item>
        <el-form-item label="action" prop="action">
          <el-input v-model="ruleFormC.action" placeholder="login"></el-input>
        </el-form-item>
        <el-form-item label="uuID" prop="uuID">
          <el-input v-model="ruleFormC.uuID" placeholder="Dapp server"></el-input>
        </el-form-item>
        <el-form-item label="loginUrl" prop="loginUrl">
          <el-input v-model="ruleFormC.loginUrl" placeholder="Dapp serverurl"></el-input>
        </el-form-item>
        <el-form-item label="appKey" prop="appKey">
          <el-input v-model="ruleFormC.appKey" placeholder="Dappapp"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm_v3('ruleFormC')"></el-button>
          <el-button @click="resetForm('ruleFormC')"></el-button>
        </el-form-item>
      </el-form>


    </div>
  </div>
</template>
<style>
  .el-box{
    width: 800px;
    margin: auto;
  }
</style>
<script>
    import ElFormItem from "../node_modules/element-ui/packages/form/src/form-item.vue";
    import ElInput from "../node_modules/element-ui/packages/input/src/input.vue";
    import QRCode from "./components/QRCode";
    export default {
        components: {
            ElInput,
            ElFormItem,
            QRCode
        },
        data() {
            return {
                showCode: false,
                qrCode: '',
                ruleFormA: {
                    version: '',
                    appName: '',
                    appIcon: '',
                    action: '',
                    uuID: '',
                    loginUrl: '',
                    timestamp: '',
                    sign: '',
                    account: '',
                    ref: ''
                },
                ruleFormB: {
                    version: '',
                    appName: '',
                    appIcon: '',
                    action: '',
                    uuID: '',
                    loginUrl: '',
                    timestamp: '',
                    sign: '',
                    account: '',
                    ref: ''
                },
                ruleFormC: {
                    version: '',
                    appName: '',
                    appIcon: '',
                    action: '',
                    uuID: '',
                    loginUrl: '',
                    timestamp: '',
                    sign: '',
                    account: '',
                    ref: '',
                    appKey: '',
                },
                rules: {
                    version: [
                        { required: true, message: 'version', trigger: 'blur' },
                    ],
                    appName: [
                        { required: true, message: 'appName', trigger: 'blur' },
                    ],
                    appIcon: [
                        { required: true, message: 'appIcon', trigger: 'blur' },
                    ],
                    action: [
                        { required: true, message: 'action', trigger: 'blur' },
                    ],
                    uuID: [
                        { required: true, message: 'uuID', trigger: 'blur' },
                    ],
                    loginUrl: [
                        { required: true, message: 'loginUrl', trigger: 'blur' },
                    ],
                    timestamp: [
                        { required: true, message: 'timestamp', trigger: 'blur' },
                    ],
                    sign: [
                        { required: true, message: 'eos', trigger: 'blur' },
                    ],
                    account: [
                        { required: true, message: 'eos', trigger: 'blur' },
                    ],
                    ref: [
                        { required: true, message: ',', trigger: 'blur' },
                    ],
                    appKey: [
                        { required: true, message: 'app', trigger: 'blur' },
                    ]


                }
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.showCode = true;
                        this.qrCode = JSON.stringify(this.ruleFormA);
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            submitForm_v2(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            submitForm_v3(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.showCode = false;
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style>
#app {
  font-family: Helvetica, sans-serif;
  text-align: center;
}
</style>



