<template>
    <div class="c-code">
        <vue-bstcode :text="text" :size="size" :logoSrc="logoSrc" :margin="margin"
                     :colorDark="colorDark" :colorLight="colorLight" :bgSrc="bgSrc"
                     :backgroundDimming="backgroundDimming" :logoScale="logoScale" :logoMargin="logoMargin"
                     :logoCornerRadius="logoCornerRadius" :whiteMargin="whiteMargin" :dotScale="dotScale"
                     :autoColor="autoColor" :binarize="binarize" :binarizeThreshold="binarizeThreshold"
                     :callback="callback" :correctLevel="correctLevel"></vue-bstcode>

    </div>
</template>

<script>
    import VueBstcode from 'vue-bstcode';

    export default {
        name: 'QRCode',
        props: {
            text: String || Number, // 
            size: Number, // , , 
            logoSrc: String, // logo
            margin: Number, // ,20px
            colorDark: String, // 
            colorLight: String, // 
            bgSrc: String, // 
            backgroundDimming: String, // 
            logoScale: String, // logo 0.2f
            logoMargin: String, // logo,0
            logoCornerRadius: Number, // logo,0
            whiteMargin: Boolean, // ,true
            dotScale: String, // ,:1.0f
            autoColor: Boolean, // true,
            binarize: Boolean, // true,,,false
            binarizeThreshold: String, // 
            callback: Function, // data URI
            correctLevel: String // L: 1M: 0Q: 3H: 20
        },
        components: {
            VueBstcode
        }
    }
    ;
</script>



